define('winolog/router', ['exports', 'ember', 'winolog/config/environment'], function (exports, Ember, config) {

	'use strict';

	var Router = Ember['default'].Router.extend({
		location: config['default'].locationType
	});

	Router.map(function () {
		this.route("cellar");
		this.route("library");
		this.route("profile");
		this.route("login");
	});

	exports['default'] = Router;

});